/**
 * PropertyEditForm Component
 * Form for editing property information
 */
import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { 
  Save, 
  X, 
  MapPin, 
  Bed, 
  Bath, 
  Square, 
  Car,
  DollarSign
} from 'lucide-react';
import { PropertyImageManager, PropertyImage } from './PropertyImageManager';
import { useAuth } from '@/contexts/AuthContext';

export interface PropertyFormData {
  title: string;
  description: string;
  price: number;
  originalPrice: number;
  discount: number;
  bedrooms: number;
  bathrooms: number;
  area: number;
  parking: number;
  latitude: string;
  longitude: string;
  nearby: string;
  cityId: number;
  typePropertyId: number;
  userId: string;
  images?: PropertyImage[];
}

interface PropertyEditFormProps {
  property?: PropertyFormData;
  onSave: (data: PropertyFormData) => void;
  onCancel: () => void;
  loading?: boolean;
}

export const PropertyEditForm: React.FC<PropertyEditFormProps> = ({
  property,
  onSave,
  onCancel,
  loading = false
}) => {
  const { user } = useAuth();
  
  const [formData, setFormData] = useState<PropertyFormData>({
    title: '',
    description: '',
    price: 0,
    originalPrice: 0,
    discount: 0,
    bedrooms: 0,
    bathrooms: 0,
    area: 0,
    parking: 0,
    latitude: '',
    longitude: '',
    nearby: '',
    cityId: 0,
    typePropertyId: 0,
    userId: user?.userId || '', // Use userId from auth context
    images: []
  });

  const [errors, setErrors] = useState<Partial<PropertyFormData>>({});

  // Update userId when user changes
  useEffect(() => {
    if (user?.userId) {
      setFormData(prev => ({
        ...prev,
        userId: user.userId
      }));
    }
  }, [user]);

  // Initialize form data when property changes
  useEffect(() => {
    if (property) {
      setFormData(property);
    }
  }, [property]);

  const handleInputChange = (field: keyof PropertyFormData, value: string | number) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }));
    
    // Clear error when user starts typing
    if (errors[field]) {
      setErrors(prev => ({
        ...prev,
        [field]: undefined
      }));
    }
  };

  const handleImagesChange = (images: PropertyImage[]) => {
    console.log('PropertyEditForm: Images changed:', { 
      newImageCount: images.length, 
      images: images.map(img => ({ id: img.id, name: img.name, isNew: img.isNew }))
    });
    
    setFormData(prev => ({
      ...prev,
      images: images
    }));
  };

  const validateForm = (): boolean => {
    const newErrors: Partial<PropertyFormData> = {};

    if (!formData.title.trim()) {
      newErrors.title = 'Title is required';
    }

    if (!formData.description.trim()) {
      newErrors.description = 'Description is required';
    }

    if (formData.price <= 0) {
      newErrors.price = 'Price must be greater than 0';
    }

    if (formData.bedrooms < 0) {
      newErrors.bedrooms = 'Bedrooms cannot be negative';
    }

    if (formData.bathrooms < 0) {
      newErrors.bathrooms = 'Bathrooms cannot be negative';
    }

    if (formData.area <= 0) {
      newErrors.area = 'Area must be greater than 0';
    }

    if (formData.discount < 0 || formData.discount > 100) {
      newErrors.discount = 'Discount must be between 0 and 100';
    }

    if (!formData.latitude.trim()) {
      newErrors.latitude = 'Latitude is required';
    }

    if (!formData.longitude.trim()) {
      newErrors.longitude = 'Longitude is required';
    }

    if (!formData.nearby.trim()) {
      newErrors.nearby = 'Nearby location is required';
    }

    if (formData.cityId <= 0) {
      newErrors.cityId = 'City ID is required';
    }

    if (formData.typePropertyId <= 0) {
      newErrors.typePropertyId = 'Property type ID is required';
    }

    if (!formData.userId.trim()) {
      newErrors.userId = 'User ID is required';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    if (validateForm()) {
      onSave(formData);
    }
  };

  const handleCancel = () => {
    onCancel();
  };

  return (
    <div className="max-w-4xl mx-auto p-6">
      <Card className="admin-card">
        <CardHeader>
          <CardTitle className="flex items-center justify-between">
            <span>Edit Property</span>
            <Button
              variant="outline"
              size="sm"
              onClick={handleCancel}
              disabled={loading}
            >
              <X className="w-4 h-4 mr-2" />
              Cancel
            </Button>
          </CardTitle>
        </CardHeader>
        
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-6">
            {/* Basic Information */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="space-y-4">
                <h3 className="text-lg font-semibold text-gray-900">Basic Information</h3>
                
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Title *
                  </label>
                  <Input
                    value={formData.title}
                    onChange={(e) => handleInputChange('title', e.target.value)}
                    placeholder="Property title"
                    className={errors.title ? 'border-red-500' : ''}
                  />
                  {errors.title && (
                    <p className="text-red-500 text-sm mt-1">{errors.title}</p>
                  )}
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Description *
                  </label>
                  <textarea
                    value={formData.description}
                    onChange={(e) => handleInputChange('description', e.target.value)}
                    placeholder="Property description"
                    rows={4}
                    className={`w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-primary ${
                      errors.description ? 'border-red-500' : 'border-gray-300'
                    }`}
                  />
                  {errors.description && (
                    <p className="text-red-500 text-sm mt-1">{errors.description}</p>
                  )}
                </div>
              </div>

              <div className="space-y-4">
                <h3 className="text-lg font-semibold text-gray-900">Pricing</h3>
                
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    <DollarSign className="w-4 h-4 inline mr-1" />
                    Price *
                  </label>
                  <Input
                    type="number"
                    value={formData.price}
                    onChange={(e) => handleInputChange('price', parseFloat(e.target.value) || 0)}
                    placeholder="0"
                    className={errors.price ? 'border-red-500' : ''}
                  />
                  {errors.price && (
                    <p className="text-red-500 text-sm mt-1">{errors.price}</p>
                  )}
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Original Price
                  </label>
                  <Input
                    type="number"
                    value={formData.originalPrice}
                    onChange={(e) => handleInputChange('originalPrice', parseFloat(e.target.value) || 0)}
                    placeholder="0"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Discount (%)
                  </label>
                  <Input
                    type="number"
                    value={formData.discount}
                    onChange={(e) => handleInputChange('discount', parseFloat(e.target.value) || 0)}
                    placeholder="0"
                    min="0"
                    max="100"
                    className={errors.discount ? 'border-red-500' : ''}
                  />
                  {errors.discount && (
                    <p className="text-red-500 text-sm mt-1">{errors.discount}</p>
                  )}
                </div>
              </div>
            </div>

            {/* Property Details */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="space-y-4">
                <h3 className="text-lg font-semibold text-gray-900">Property Details</h3>
                
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      <Bed className="w-4 h-4 inline mr-1" />
                      Bedrooms
                    </label>
                    <Input
                      type="number"
                      value={formData.bedrooms}
                      onChange={(e) => handleInputChange('bedrooms', parseInt(e.target.value) || 0)}
                      placeholder="0"
                      min="0"
                      className={errors.bedrooms ? 'border-red-500' : ''}
                    />
                    {errors.bedrooms && (
                      <p className="text-red-500 text-sm mt-1">{errors.bedrooms}</p>
                    )}
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      <Bath className="w-4 h-4 inline mr-1" />
                      Bathrooms
                    </label>
                    <Input
                      type="number"
                      value={formData.bathrooms}
                      onChange={(e) => handleInputChange('bathrooms', parseInt(e.target.value) || 0)}
                      placeholder="0"
                      min="0"
                      className={errors.bathrooms ? 'border-red-500' : ''}
                    />
                    {errors.bathrooms && (
                      <p className="text-red-500 text-sm mt-1">{errors.bathrooms}</p>
                    )}
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    <Square className="w-4 h-4 inline mr-1" />
                    Area (sq ft) *
                  </label>
                  <Input
                    type="number"
                    value={formData.area}
                    onChange={(e) => handleInputChange('area', parseFloat(e.target.value) || 0)}
                    placeholder="0"
                    min="0"
                    className={errors.area ? 'border-red-500' : ''}
                  />
                  {errors.area && (
                    <p className="text-red-500 text-sm mt-1">{errors.area}</p>
                  )}
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    <Car className="w-4 h-4 inline mr-1" />
                    Parking Spaces
                  </label>
                  <Input
                    type="number"
                    value={formData.parking}
                    onChange={(e) => handleInputChange('parking', parseInt(e.target.value) || 0)}
                    placeholder="0"
                    min="0"
                  />
                </div>
              </div>

              <div className="space-y-4">
                <h3 className="text-lg font-semibold text-gray-900">Location</h3>
                
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    <MapPin className="w-4 h-4 inline mr-1" />
                    Nearby *
                  </label>
                  <Input
                    value={formData.nearby}
                    onChange={(e) => handleInputChange('nearby', e.target.value)}
                    placeholder="Nearby landmarks or areas"
                    className={errors.nearby ? 'border-red-500' : ''}
                  />
                  {errors.nearby && (
                    <p className="text-red-500 text-sm mt-1">{errors.nearby}</p>
                  )}
                </div>

                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      Latitude *
                    </label>
                    <Input
                      value={formData.latitude}
                      onChange={(e) => handleInputChange('latitude', e.target.value)}
                      placeholder="0.000000"
                      className={errors.latitude ? 'border-red-500' : ''}
                    />
                    {errors.latitude && (
                      <p className="text-red-500 text-sm mt-1">{errors.latitude}</p>
                    )}
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      Longitude *
                    </label>
                    <Input
                      value={formData.longitude}
                      onChange={(e) => handleInputChange('longitude', e.target.value)}
                      placeholder="0.000000"
                      className={errors.longitude ? 'border-red-500' : ''}
                    />
                    {errors.longitude && (
                      <p className="text-red-500 text-sm mt-1">{errors.longitude}</p>
                    )}
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    City ID *
                  </label>
                  <Input
                    type="number"
                    value={formData.cityId}
                    onChange={(e) => handleInputChange('cityId', parseInt(e.target.value) || 0)}
                    placeholder="0"
                    min="0"
                    className={errors.cityId ? 'border-red-500' : ''}
                  />
                  {errors.cityId && (
                    <p className="text-red-500 text-sm mt-1">{errors.cityId}</p>
                  )}
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Property Type ID *
                  </label>
                  <Input
                    type="number"
                    value={formData.typePropertyId}
                    onChange={(e) => handleInputChange('typePropertyId', parseInt(e.target.value) || 0)}
                    placeholder="0"
                    min="0"
                    className={errors.typePropertyId ? 'border-red-500' : ''}
                  />
                  {errors.typePropertyId && (
                    <p className="text-red-500 text-sm mt-1">{errors.typePropertyId}</p>
                  )}
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    User ID *
                  </label>
                  <Input
                    value={formData.userId}
                    onChange={(e) => handleInputChange('userId', e.target.value)}
                    placeholder="User ID"
                    className={errors.userId ? 'border-red-500' : ''}
                    disabled={!!user?.userId} // Disable if user is logged in
                  />
                  {user?.userId && (
                    <p className="text-sm text-gray-500 mt-1">Automatically set from your account</p>
                  )}
                  {errors.userId && (
                    <p className="text-red-500 text-sm mt-1">{errors.userId}</p>
                  )}
                </div>
              </div>
            </div>

            {/* Property Images */}
            <div className="mt-6">
              <div className={`mb-4 p-3 border rounded-lg ${
                (formData.images?.length || 0) === 0 
                  ? 'bg-yellow-50 border-yellow-200' 
                  : 'bg-blue-50 border-blue-200'
              }`}>
                <p className={`text-sm ${
                  (formData.images?.length || 0) === 0 
                    ? 'text-yellow-700' 
                    : 'text-blue-700'
                }`}>
                  <strong>Images Status:</strong> {formData.images?.length || 0} images selected
                  {formData.images && formData.images.length > 0 && (
                    <span className="ml-2">
                      ({formData.images.filter(img => img.isNew).length} new, {formData.images.filter(img => !img.isNew).length} existing)
                    </span>
                  )}
                  {(formData.images?.length || 0) === 0 && (
                    <span className="ml-2 text-yellow-600">
                      (All images will be removed when saved)
                    </span>
                  )}
                </p>
              </div>
              <PropertyImageManager
                images={formData.images || []}
                onImagesChange={handleImagesChange}
                maxImages={10}
              />
            </div>

            {/* Form Actions */}
            <div className="flex justify-end space-x-4 pt-6 border-t">
              <Button
                type="button"
                variant="outline"
                onClick={handleCancel}
                disabled={loading}
              >
                Cancel
              </Button>
              <Button
                type="submit"
                disabled={loading}
                className="min-w-[120px]"
              >
                {loading ? (
                  <>
                    <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin mr-2" />
                    Saving...
                  </>
                ) : (
                  <>
                    <Save className="w-4 h-4 mr-2" />
                    Save Changes
                  </>
                )}
              </Button>
            </div>
          </form>
        </CardContent>
      </Card>
    </div>
  );
};
