/**
 * Property Entity
 * Represents a property in the domain layer
 */

export interface PropertyUser {
  id: string;
  name?: string;
  email?: string;
  [key: string]: unknown;
}

export interface PropertyInvestment {
  id: string;
  name?: string;
  [key: string]: unknown;
}

export interface PropertyData {
  id: number;
  title: string;
  description?: string;
  price: number;
  originalPrice?: number;
  discount?: number;
  bedrooms: number;
  bathrooms: number;
  area: number;
  parking?: number;
  rating?: number;
  views?: number;
  status: string;
  createdAt: string;
  updatedAt?: string;
  latitude?: string;
  longitude?: string;
  nearby?: string;
  city?: string | null;
  cityId?: number;
  typeProperty?: string | null;
  typePropertyId?: number;
  user?: PropertyUser | null;
  userId?: string;
  images?: string[] | null;
  imagesProperty?: string[] | null;
  investment?: PropertyInvestment | null;
}

export interface APIPropertyData {
  id: number;
  title: string;
  description?: string;
  createdAt: string;
  updatedAt?: string;
  rating?: number;
  views?: number;
  status: string;
  price: number;
  originalPrice?: number;
  discount?: number;
  bedrooms: number;
  bathrooms: number;
  area: number;
  parking?: number;
  latitude?: string;
  longitude?: string;
  nearby?: string;
  city?: string | null;
  cityId?: number;
  typeProperty?: string | null;
  typePropertyId?: number;
  user?: PropertyUser | null;
  userId?: string;
  images?: string[] | null;
  imagesProperty?: string[] | null;
  investment?: PropertyInvestment | null;
}

export class Property {
  public readonly id: number;
  public readonly title: string;
  public readonly description?: string;
  public readonly price: number;
  public readonly originalPrice?: number;
  public readonly discount?: number;
  public readonly bedrooms: number;
  public readonly bathrooms: number;
  public readonly area: number;
  public readonly parking?: number;
  public readonly rating?: number;
  public readonly views?: number;
  public readonly status: string;
  public readonly createdAt: string;
  public readonly updatedAt?: string;
  public readonly latitude?: string;
  public readonly longitude?: string;
  public readonly nearby?: string;
  public readonly city?: string | null;
  public readonly cityId?: number;
  public readonly typeProperty?: string | null;
  public readonly typePropertyId?: number;
  public readonly user?: PropertyUser | null;
  public readonly userId?: string;
  public readonly images?: string[] | null;
  public readonly imagesProperty?: string[] | null;
  public readonly investment?: PropertyInvestment | null;

  constructor({
    id,
    title,
    description,
    price,
    originalPrice,
    discount,
    bedrooms,
    bathrooms,
    area,
    parking,
    rating,
    views,
    status,
    createdAt,
    updatedAt,
    latitude,
    longitude,
    nearby,
    city,
    cityId,
    typeProperty,
    typePropertyId,
    user,
    userId,
    images,
    imagesProperty,
    investment
  }: PropertyData) {
    this.id = id;
    this.title = title;
    this.description = description;
    this.price = price;
    this.originalPrice = originalPrice;
    this.discount = discount;
    this.bedrooms = bedrooms;
    this.bathrooms = bathrooms;
    this.area = area;
    this.parking = parking;
    this.rating = rating;
    this.views = views;
    this.status = status;
    this.createdAt = createdAt;
    this.updatedAt = updatedAt;
    this.latitude = latitude;
    this.longitude = longitude;
    this.nearby = nearby;
    this.city = city;
    this.cityId = cityId;
    this.typeProperty = typeProperty;
    this.typePropertyId = typePropertyId;
    this.user = user;
    this.userId = userId;
    this.images = images;
    this.imagesProperty = imagesProperty;
    this.investment = investment;
  }

  /**
   * Get formatted price
   */
  getFormattedPrice() {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD',
    }).format(this.price);
  }

  /**
   * Get property summary
   */
  getSummary() {
    return `${this.bedrooms} bed, ${this.bathrooms} bath • ${this.area} sq ft`;
  }

  /**
   * Check if property is available
   */
  isAvailable() {
    return this.status === '1' || this.status === 'active' || this.status === 'available';
  }

  /**
   * Get primary image URL
   */
  getPrimaryImage() {
    return this.images && this.images.length > 0 ? this.images[0] : null;
  }

  /**
   * Get location string
   */
  getLocation() {
    return this.city || this.nearby || 'Unknown location';
  }

  /**
   * Get property type
   */
  getPropertyType() {
    return this.typeProperty || 'Unknown type';
  }

  /**
   * Get discount percentage
   */
  getDiscountPercentage() {
    return this.discount || 0;
  }

  /**
   * Get final price after discount
   */
  getFinalPrice() {
    if (this.discount && this.discount > 0) {
      return this.price * (1 - this.discount / 100);
    }
    return this.price;
  }

  /**
   * Get formatted final price
   */
  getFormattedFinalPrice() {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD',
    }).format(this.getFinalPrice());
  }

  /**
   * Validate property data
   */
  isValid() {
    return !!(
      this.id &&
      this.title &&
      this.price &&
      this.bedrooms &&
      this.bathrooms &&
      this.area
    );
  }

  /**
   * Convert to plain object
   */
  toJSON() {
    return {
      id: this.id,
      title: this.title,
      description: this.description,
      price: this.price,
      originalPrice: this.originalPrice,
      discount: this.discount,
      bedrooms: this.bedrooms,
      bathrooms: this.bathrooms,
      area: this.area,
      parking: this.parking,
      rating: this.rating,
      views: this.views,
      status: this.status,
      createdAt: this.createdAt,
      updatedAt: this.updatedAt,
      latitude: this.latitude,
      longitude: this.longitude,
      nearby: this.nearby,
      city: this.city,
      cityId: this.cityId,
      typeProperty: this.typeProperty,
      typePropertyId: this.typePropertyId,
      user: this.user,
      userId: this.userId,
      images: this.images,
      imagesProperty: this.imagesProperty,
      investment: this.investment
    };
  }

  /**
   * Create Property instance from API response
   */
  static fromAPIResponse(apiData: APIPropertyData): Property {
    return new Property({
      id: apiData.id,
      title: apiData.title,
      description: apiData.description,
      price: apiData.price,
      originalPrice: apiData.originalPrice,
      discount: apiData.discount,
      bedrooms: apiData.bedrooms,
      bathrooms: apiData.bathrooms,
      area: apiData.area,
      parking: apiData.parking,
      rating: apiData.rating,
      views: apiData.views,
      status: apiData.status,
      createdAt: apiData.createdAt,
      updatedAt: apiData.updatedAt,
      latitude: apiData.latitude,
      longitude: apiData.longitude,
      nearby: apiData.nearby,
      city: apiData.city,
      cityId: apiData.cityId,
      typeProperty: apiData.typeProperty,
      typePropertyId: apiData.typePropertyId,
      user: apiData.user,
      userId: apiData.userId,
      images: apiData.images,
      imagesProperty: apiData.imagesProperty,
      investment: apiData.investment
    });
  }
}
