/**
 * PropertyEditPage Component
 * Page for editing property information
 */
import React, { useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { PropertyEditForm, PropertyFormData } from '../components/PropertyEditForm';
import { PropertyImage } from '../components/PropertyImageManager';
import { Property } from '../../domain/entities/Property';
import { propertyRepository } from '../../data/repository/propertyRepository';
import { propertyApi } from '../../data/api/propertyApi';
import { toast } from 'sonner';


export const PropertyEditPage: React.FC = () => {
  const navigate = useNavigate();
  const { id } = useParams<{ id: string }>();
  const [property, setProperty] = useState<Property | null>(null);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // Load property data
  useEffect(() => {
    const loadProperty = async () => {
      if (!id) {
        setError('Property ID is required');
        setLoading(false);
        return;
      }

      try {
        setLoading(true);
        setError(null);
        
        const propertyData = await propertyRepository.getPropertyById(parseInt(id));
        setProperty(propertyData);
      } catch (err) {
        console.error('Error loading property:', err);
        setError('Failed to load property data');
      } finally {
        setLoading(false);
      }
    };

    loadProperty();
  }, [id]);

  // Convert Property entity to form data
  const propertyToFormData = (prop: Property): PropertyFormData => {
    // Convert images array to PropertyImage array (use imagesProperty if available, fallback to images)
    const imageUrls = prop.imagesProperty || prop.images || [];
    
    // Debug: Log the original image paths from the property
    console.log('Loading property images:', {
      propertyId: prop.id,
      imagesProperty: prop.imagesProperty,
      images: prop.images,
      finalImageUrls: imageUrls
    });
    
    const images: PropertyImage[] = imageUrls.map((url, index) => ({
      id: `existing-${index}`,
      url: url,
      name: `Image ${index + 1}`,
      isNew: false
    }));

    return {
      title: prop.title,
      description: prop.description || '',
      price: prop.price,
      originalPrice: prop.originalPrice || 0,
      discount: prop.discount || 0,
      bedrooms: prop.bedrooms,
      bathrooms: prop.bathrooms,
      area: prop.area,
      parking: prop.parking || 0,
      latitude: prop.latitude || '',
      longitude: prop.longitude || '',
      nearby: prop.nearby || '',
      cityId: prop.cityId || 0,
      typePropertyId: prop.typePropertyId || 0,
      userId: prop.userId || '',
      images: images
    };
  };

  // Handle form save
  const handleSave = async (formData: PropertyFormData) => {
    if (!property) return;

    try {
      setSaving(true);
      setError(null);

      // Process images - separate new files from existing URLs
      const newImageFiles: File[] = [];
      const existingImageUrls: string[] = [];

      // Debug: Log all images in the form
      console.log('Processing form images:', formData.images);

      (formData.images || []).forEach(image => {
        if (image.isNew && image.file) {
          newImageFiles.push(image.file);
          console.log('New image file:', image.file.name);
        } else if (!image.isNew) {
          // Only include existing images that are still in the form (not deleted)
          existingImageUrls.push(image.url);
          console.log('Keeping existing image path:', image.url);
        }
      });

      // Debug: Log what was removed
      const originalImageUrls = property.imagesProperty || property.images || [];
      const removedImageUrls = originalImageUrls.filter(url => !existingImageUrls.includes(url));
      console.log('Image removal analysis:', {
        originalPaths: originalImageUrls,
        keptPaths: existingImageUrls,
        removedPaths: removedImageUrls
      });

      // Handle images - upload new files and combine with existing URLs
      let allImageUrls: string[] = [...existingImageUrls];
      
      // Upload new image files if any
      if (newImageFiles.length > 0) {
        try {
          const uploadResult = await propertyApi.uploadPropertyImages(property.id, newImageFiles);
          console.log('Image upload result:', uploadResult);
          
          // Extract URLs from upload result
          if (uploadResult && Array.isArray(uploadResult)) {
            allImageUrls = [...allImageUrls, ...uploadResult];
          } else if (uploadResult && uploadResult.urls && Array.isArray(uploadResult.urls)) {
            allImageUrls = [...allImageUrls, ...uploadResult.urls];
          }
        } catch (uploadError) {
          console.warn('Failed to upload new images:', uploadError);
        }
      }

      // Debug logging
      console.log('Image processing:', {
        totalImagesInForm: formData.images?.length || 0,
        newImageFiles: newImageFiles.length,
        existingImageUrls: existingImageUrls.length,
        finalImageUrls: allImageUrls.length,
        allImageUrls,
        willSendToAPI: allImageUrls
      });

      // Create updated property data in the exact format expected by the API
      // Note: API expects lowercase field names (as shown in successful response)
      const updatedPropertyData: any = {
        title: formData.title,
        description: formData.description,
        price: formData.price,
        originalPrice: formData.originalPrice,
        discount: formData.discount,
        bedrooms: formData.bedrooms,
        bathrooms: formData.bathrooms,
        area: formData.area,
        parking: formData.parking,
        latitude: formData.latitude,
        longitude: formData.longitude,
        nearby: formData.nearby,
        cityId: formData.cityId,
        typePropertyId: formData.typePropertyId,
        userId: formData.userId
      };

      // Handle images according to C# model structure
      // Files: List<IFormFile>? - actual image files
      // ImagesProperty: List<string>? - filenames of the images
      
      // Add Files for new image uploads (if any)
      if (newImageFiles.length > 0) {
        updatedPropertyData.Files = newImageFiles;
      }
      
      // Add ImagesProperty with filenames (if any)
      if (newImageFiles.length > 0) {
        const imageFilenames = newImageFiles.map(file => file.name);
        updatedPropertyData.ImagesProperty = imageFilenames;
      }

      // Debug: Log the final data being sent to API
      console.log('Final API payload:', updatedPropertyData);

      // Update property via API
      const updateResult = await propertyApi.updateProperty(property.id, updatedPropertyData);
      
      // Debug: Log the API response
      console.log('API update response:', updateResult);
      
      // Show success message with image details
      const imageMessage = allImageUrls.length > 0 
        ? ` with ${allImageUrls.length} image${allImageUrls.length === 1 ? '' : 's'}`
        : ' (no images)';
      toast.success(`Property updated successfully${imageMessage}!`);
      
      // Navigate back to properties list
      navigate('/admin/properties');
    } catch (err) {
      console.error('Error saving property:', err);
      const errorMessage = err instanceof Error ? err.message : 'Failed to save property changes';
      setError(errorMessage);
      toast.error(errorMessage);
    } finally {
      setSaving(false);
    }
  };

  // Handle form cancel
  const handleCancel = () => {
    navigate('/admin/properties');
  };

  // Loading state
  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-center">
          <div className="w-8 h-8 border-4 border-primary border-t-transparent rounded-full animate-spin mx-auto mb-4" />
          <p className="text-gray-600">Loading property data...</p>
        </div>
      </div>
    );
  }

  // Error state
  if (error) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-center">
          <div className="w-16 h-16 bg-red-100 rounded-full flex items-center justify-center mx-auto mb-4">
            <svg className="w-8 h-8 text-red-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-2.5L13.732 4c-.77-.833-1.964-.833-2.732 0L3.732 16.5c-.77.833.192 2.5 1.732 2.5z" />
            </svg>
          </div>
          <h3 className="text-lg font-semibold text-gray-900 mb-2">Error Loading Property</h3>
          <p className="text-gray-600 mb-4">{error}</p>
          <button
            onClick={() => navigate('/admin/properties')}
            className="px-4 py-2 bg-primary text-white rounded-md hover:bg-primary-dark transition-colors"
          >
            Back to Properties
          </button>
        </div>
      </div>
    );
  }

  // No property found
  if (!property) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-center">
          <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-4">
            <svg className="w-8 h-8 text-gray-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 11H5m14 0a2 2 0 012 2v6a2 2 0 01-2 2H5a2 2 0 01-2-2v-6a2 2 0 012-2m14 0V9a2 2 0 00-2-2M5 11V9a2 2 0 012-2m0 0V5a2 2 0 012-2h6a2 2 0 012 2v2M7 7h10" />
            </svg>
          </div>
          <h3 className="text-lg font-semibold text-gray-900 mb-2">Property Not Found</h3>
          <p className="text-gray-600 mb-4">The property you're looking for doesn't exist.</p>
          <button
            onClick={() => navigate('/admin/properties')}
            className="px-4 py-2 bg-primary text-white rounded-md hover:bg-primary-dark transition-colors"
          >
            Back to Properties
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-4">
      {/* Page Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Edit Property</h1>
          <p className="text-gray-600 mt-1">Modify property information and details</p>
        </div>
      </div>

      {/* Error Message */}
      {error && (
        <div className="bg-red-50 border border-red-200 rounded-md p-4">
          <div className="flex">
            <div className="flex-shrink-0">
              <svg className="h-5 w-5 text-red-400" viewBox="0 0 20 20" fill="currentColor">
                <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
              </svg>
            </div>
            <div className="ml-3">
              <h3 className="text-sm font-medium text-red-800">Error</h3>
              <div className="mt-2 text-sm text-red-700">
                <p>{error}</p>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Edit Form */}
      <PropertyEditForm
        property={propertyToFormData(property)}
        onSave={handleSave}
        onCancel={handleCancel}
        loading={saving}
      />
    </div>
  );
};
