/**
 * AuthContext
 * Manages authentication state throughout the application
 */
import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';

interface User {
  phoneNumber: string;
  userId: string;
  isAuthenticated: boolean;
}

interface AuthContextType {
  user: User | null;
  login: (phoneNumber: string, password: string) => Promise<{ success: boolean; message?: string }>;
  logout: () => void;
  isLoading: boolean;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

interface AuthProviderProps {
  children: ReactNode;
}

export const AuthProvider: React.FC<AuthProviderProps> = ({ children }) => {
  const [user, setUser] = useState<User | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  // Check for existing authentication on mount
  useEffect(() => {
    const checkAuth = () => {
      const isAuthenticated = localStorage.getItem('isAuthenticated') === 'true';
      const phoneNumber = localStorage.getItem('phoneNumber');
      const userId = localStorage.getItem('userId');

      if (isAuthenticated && phoneNumber && userId) {
        setUser({
          phoneNumber,
          userId,
          isAuthenticated: true
        });
      }
      
      setIsLoading(false);
    };

    checkAuth();
  }, []);

  const login = async (phoneNumber: string, password: string): Promise<{ success: boolean; message?: string }> => {
    try {
      setIsLoading(true);
      
      // Call the real login API
      const response = await fetch('http://161.97.100.109:7001/api/Account/Login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          phoneNumber,
          password
        })
      });

      // Check if the response is successful (200 status)
      if (response.ok) {
        // Parse the response to get user data including userId
        const responseData = await response.json();
        console.log('Login response data:', responseData);
        
        // Extract userId from response (adjust field name based on your API response)
        const userId = responseData.userId || responseData.id || responseData.user?.id || phoneNumber;
        
        const userData = {
          phoneNumber,
          userId: userId.toString(),
          isAuthenticated: true
        };

        setUser(userData);
        localStorage.setItem('isAuthenticated', 'true');
        localStorage.setItem('phoneNumber', phoneNumber);
        localStorage.setItem('userId', userId.toString());
        
        return { success: true };
      } else {
        // Login failed - try to get error message from response
        let errorMessage = 'Invalid phone number or password. Please try again.';
        
        try {
          const errorData = await response.json();
          if (errorData.message) {
            errorMessage = errorData.message;
          } else if (errorData.error) {
            errorMessage = errorData.error;
          }
        } catch (parseError) {
          // If we can't parse the error response, use default message
          console.warn('Could not parse error response:', parseError);
        }
        
        return { success: false, message: errorMessage };
      }
    } catch (error) {
      console.error('Login error:', error);
      return { 
        success: false, 
        message: 'Network error. Please check your connection and try again.' 
      };
    } finally {
      setIsLoading(false);
    }
  };

  const logout = () => {
    setUser(null);
    localStorage.removeItem('isAuthenticated');
    localStorage.removeItem('phoneNumber');
    localStorage.removeItem('userId');
  };

  const value: AuthContextType = {
    user,
    login,
    logout,
    isLoading
  };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = (): AuthContextType => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};
