/**
 * Property API
 * Handles all API calls related to properties
 */

const API_BASE_URL = 'http://161.97.100.109:7001/api/Property';

/**
 * Custom error class for API errors
 */
class APIError extends Error {
  status: number;
  data: any;
  
  constructor(message: string, status: number, data: any) {
    super(message);
    this.name = 'APIError';
    this.status = status;
    this.data = data;
  }
}

/**
 * Generic API request handler
 */
const apiRequest = async (url: string, options: RequestInit = {}) => {
  const defaultOptions = {
    headers: {
      'Content-Type': 'application/json',
      'Accept': 'application/json',
    },
    ...options,
  };

  try {
    const response = await fetch(url, defaultOptions);
    
    if (!response.ok) {
      const errorData = await response.json().catch(() => ({}));
      throw new APIError(
        `API request failed: ${response.statusText}`,
        response.status,
        errorData
      );
    }

    const data = await response.json();
    return data;
  } catch (error) {
    if (error instanceof APIError) {
      throw error;
    }
    
    // Network or other errors
    throw new APIError(
      `Network error: ${error instanceof Error ? error.message : String(error)}`,
      0,
      null
    );
  }
};

/**
 * Property API methods
 */
export const propertyApi = {
  /**
   * Get all properties
   */
  async getAllProperties() {
    try {
      const data = await apiRequest(`${API_BASE_URL}/getAllProperty`);
      
      // Handle different response structures
      if (Array.isArray(data)) {
        return data;
      }
      
      if (data.data && Array.isArray(data.data)) {
        return data.data;
      }
      
      if (data.properties && Array.isArray(data.properties)) {
        return data.properties;
      }
      
      // If response is an object with property data
      if (data.id || data.title || data.name) {
        return [data];
      }
      
      // Fallback - return empty array if structure is unexpected
      console.warn('Unexpected API response structure:', data);
      return [];
    } catch (error) {
      console.error('Error fetching properties:', error);
      throw error;
    }
  },

  /**
   * Get property by ID
   */
  async getPropertyById(id: string | number) {
    try {
      const data = await apiRequest(`${API_BASE_URL}/PropertyById/${id}`);
      return data;
    } catch (error) {
      console.error(`Error fetching property ${id}:`, error);
      throw error;
    }
  },

  /**
   * Create new property
   */
  async createProperty(propertyData: Record<string, unknown>) {
    try {
      // Server expects [FromForm] so always use FormData
      const formData = new FormData();
      
      console.log('createProperty - propertyData:', propertyData);
      console.log('Creating FormData for [FromForm] endpoint...');
      console.log('Property data keys:', Object.keys(propertyData));
      
      // Add all property data fields
      Object.keys(propertyData).forEach(key => {
        if (key === 'Files') {
          // Add files to FormData
          const files = propertyData.Files as File[];
          console.log('Adding Files to FormData:', files.length, 'files');
          files.forEach((file, index) => {
            console.log(`Adding file ${index}:`, file.name, file.type, file.size);
            formData.append(`Files[${index}]`, file);
          });
        } else {
          // Add other fields as strings
          console.log(`Adding field ${key}:`, propertyData[key]);
          formData.append(key, String(propertyData[key]));
        }
      });
      
      // Debug: Log FormData contents
      console.log('FormData entries:');
      for (const [key, value] of formData.entries()) {
        console.log(`${key}:`, value);
      }
      
      console.log('Sending FormData to API...');
      const response = await fetch(`${API_BASE_URL}/addNewProperty`, {
        method: 'POST',
        body: formData,
      });
      
      console.log('API Response status:', response.status);
      console.log('API Response headers:', Object.fromEntries(response.headers.entries()));
      
      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        console.error('API Error:', errorData);
        throw new APIError(
          `API request failed: ${response.statusText}`,
          response.status,
          errorData
        );
      }
      
      const result = await response.json();
      console.log('API Success Response:', result);
      return result;
    } catch (error) {
      console.error('Error creating property:', error);
      throw error;
    }
  },

  /**
   * Update property
   */
  async updateProperty(id: string | number, propertyData: Record<string, unknown>) {
    try {
      // Check if we have files to upload
      const hasFiles = propertyData.Files && Array.isArray(propertyData.Files) && propertyData.Files.length > 0;
      
      if (hasFiles) {
        // Use FormData for file uploads
        const formData = new FormData();
        
        // Add all property data fields
        Object.keys(propertyData).forEach(key => {
          if (key === 'Files') {
            // Add files to FormData
            const files = propertyData.Files as File[];
            files.forEach((file, index) => {
              formData.append(`Files[${index}]`, file);
            });
          } else if (key === 'ImagesProperty') {
            // Add ImagesProperty as JSON string
            const images = propertyData.ImagesProperty as string[];
            formData.append('ImagesProperty', JSON.stringify(images));
          } else {
            // Add other fields as strings
            formData.append(key, String(propertyData[key]));
          }
        });
        
        const response = await fetch(`${API_BASE_URL}/updatePropertyById/${id}`, {
          method: 'PUT',
          body: formData,
        });
        
        if (!response.ok) {
          const errorData = await response.json().catch(() => ({}));
          throw new APIError(
            `API request failed: ${response.statusText}`,
            response.status,
            errorData
          );
        }
        
        return await response.json();
      } else {
        // Use JSON for non-file requests
        const data = await apiRequest(`${API_BASE_URL}/updatePropertyById/${id}`, {
          method: 'PUT',
          body: JSON.stringify(propertyData),
        });
        return data;
      }
    } catch (error) {
      console.error(`Error updating property ${id}:`, error);
      throw error;
    }
  },

  /**
   * Delete property
   */
  async deleteProperty(id: string | number) {
    try {
      const data = await apiRequest(`${API_BASE_URL}/${id}`, {
        method: 'DELETE',
      });
      return data;
    } catch (error) {
      console.error(`Error deleting property ${id}:`, error);
      throw error;
    }
  },

  /**
   * Search properties
   */
  async searchProperties(searchParams: Record<string, unknown>) {
    try {
      const queryString = new URLSearchParams(searchParams as Record<string, string>).toString();
      const data = await apiRequest(`${API_BASE_URL}/search?${queryString}`);
      
      if (Array.isArray(data)) {
        return data;
      }
      
      if (data.data && Array.isArray(data.data)) {
        return data.data;
      }
      
      return [];
    } catch (error) {
      console.error('Error searching properties:', error);
      throw error;
    }
  },

  /**
   * Upload property images
   */
  async uploadPropertyImages(propertyId: string | number, files: File[]) {
    try {
      const formData = new FormData();
      files.forEach((file) => {
        formData.append(`images`, file);
      });

      const response = await fetch(`${API_BASE_URL}/uploadImages/${propertyId}`, {
        method: 'POST',
        body: formData,
      });

      if (!response.ok) {
        throw new APIError(
          `Upload failed: ${response.statusText}`,
          response.status,
          null
        );
      }

      const data = await response.json();
      return data;
    } catch (error) {
      console.error('Error uploading images:', error);
      throw error;
    }
  }
};

export { APIError };
